        <div class="p-fluid p-grid">
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="" class="p-mt-2">Used</label>
                    <div class="p-col">
                        <p-dropdown
                            v-model="embryoTransfer.catheter"
                            :options="catheterOptions"
                        ></p-dropdown>
                    </div>
                </div>
            </div>
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="" class="p-mt-2">Lo #</label>
                    <div class="p-col">
                        <p-input-text v-model="embryoTransfer.lotNo" />
                    </div>
                </div>
            </div>
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="" class="p-mt-2">Exp. Date</label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryoTransfer.lotExpDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                        />
                    </div>
                </div>
            </div>
        </div>

        <div class="p-fluid p-grid">
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="">Blood on Catheter Tip</label>
                    <div class="p-col">
