                                    name="blood-on-catheter-tip"
                                    :value="1"
                                />
                                <label for="">Yes</label>
                            </div>
                            <div class="p-field-checkbox">
                                <p-radio-button
                                    v-model="embryoTransfer.bloodOnTip"
                                    name="blood-on-catheter-tip"
                                    :value="0"
                                />
                                <label for="">No</label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="">Mucus on Catheter Tip</label>
                    <div class="p-col">
                        <div class="p-formgroup-inline">
                            <div class="p-field-checkbox">
                                <p-radio-button
                                    v-model="embryoTransfer.mucusOnTip"
                                    name="mucus-on-catheter-tip"
                                    :value="1"
                                />
                                <label for="">Yes</label>
                            </div>
                            <div class="p-field-checkbox">
                                <p-radio-button
                                    v-model="embryoTransfer.mucusOnTip"
                                    name="mucus-on-catheter-tip"
