                                />
                                <label for="">No</label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </p-fieldset>
</template>

<script>
import { computed, inject } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const embryoTransfer = inject("embryoTransfer");
        const catheterOptions = computed(
            () => store.state.embryoTransfer.catheterList
        );
        return { embryoTransfer, catheterOptions };
    }
};
</script>
