<template>
    <p-fieldset legend="Embryos Transfered" class="p-mb-3" style="height: 100%">
        <p-data-table
            :value="transferredEmbryos"
            dataKey="id"
            class="p-datatable-sm p-mt-1"
        >
            <p-column header="Embryo" field="embryo" />
            <p-column header="Embryo Id" field="embryoId" />
            <p-column header="Stage" field="stage" />
            <p-column header="Grade" field="grade" />
        </p-data-table>
    </p-fieldset>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const transferredEmbryos = computed(
            () => store.state.embryoTransfer.transferredEmbryos
        );

        onMounted(() => {
            store
                .dispatch(
                    "embryoTransfer/getTransferredEmbryos",
                    store.state.artCycle.selectedCycle.cycleId
                )
                .then(() => {})
                .catch(() => {});
        });

        return { transferredEmbryos };
    }
};
</script>
