<template>
    <p-fieldset
        id="embryo-transfer"
        legend="Embryo Transfer"
        class="p-inputtext-sm"
    >
        <div class="p-fluid p-grid">
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="" class="p-mt-2">Date</label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryoTransfer.etDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                            :class="{
                                'p-invalid': submitted && v$.etDate.$invalid
                            }"
                        />
                    </div>
                    <small
                        v-if="
                            (v$.etDate.$invalid && submitted) ||
                                v$.etDate.$pending.$response
                        "
                        class="p-error"
                        >{{
                            v$.etDate.required.$message.replace(
                                "Value",
                                "Et Date"
                            )
                        }}
                    </small>
                </div>
            </div>
            <div class="p-col-fixed p-ml-2">
                <div class="p-field p-grid">
                    <label for="" class="p-mt-2">Days post OR</label>
                    <div class="p-col">
                        <p-dropdown
                            v-model="embryoTransfer.daysPostOR"
                            :options="daysPostOROptions"
                        ></p-dropdown>
                    </div>
                </div>
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12 p-md-4">
                <embyoo-transfer-catheter />

                <embryo-transfer-ultrasound />
            </div>

            <div class="p-col-12 p-md-4">
                <embryo-transferred-table />
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12 p-md-4">
                <embryo-transfer-trauman />
                <embryo-transfer-patient-discomfort />
            </div>
            <div class="p-col-12 p-md-4">
                <embryo-transfer-reload />
                <embryo-transfer-transfer />
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12 p-md-4">
                <p-textarea
                    v-model="embryoTransfer.notes"
                    rows="4"
                    style="width: 100%"
                />
            </div>
            <div class="p-col-12 p-md-4">
                <div class="p-field p-grid">
                    <label
                        for="firstname"
                        class="p-col-fixed"
                        style="width: 100px"
                    >
                        Embryologist
                    </label>
                    <div class="p-col">
                        <p-dropdown
                            v-model="embryoTransfer.embryologist"
                            style="width: 100%"
                            :options="embryologistOpts"
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width: 100px">
                        Clincian
                    </label>
                    <div class="p-col">
                        <p-dropdown
                            v-model="embryoTransfer.clinician"
                            type="text"
                            style="width: 100%"
                            :options="doctorOptions"
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                </div>
            </div>
        </div>

        <!-- <embyro-transfer-from /> -->

        <p-button
            label="Save"
            class="p-mr-2 p-button-success"
            @click="saveEmbryoTransfer"
            v-if="hasCreatePermission"
        />
        <p-button
            label="Delte"
            class="p-mr-2 p-button-danger"
            @click="deleteEbryoTransfer"
            v-if="hasDeletePermission"
        />
        <p-button
            label="PDF Report"
            class="p-mr-2 p-button-secondary"
            @click="openPdfReport"
        />
    </p-fieldset>

    <bron-embyro-transfer />
</template>

<script>
import { onMounted, provide, ref } from "vue";

import EmbyooTransferCatheter from "@/components/embyro-transfer/catheter.vue";
import EmbryoTransferUltrasound from "@/components/embyro-transfer/ultrasound.vue";
import EmbryoTransferTrauman from "@/components/embyro-transfer/trauman.vue";
import EmbryoTransferPatientDiscomfort from "@/components/embyro-transfer/patient-discomfort.vue";
import EmbryoTransferReload from "@/components/embyro-transfer/reaload.vue";
import EmbryoTransferTransfer from "@/components/embyro-transfer/tranfer.vue";

import EmbyroTransferFrom from "@/components/embyro-transfer/EmbyroTransferFrom.vue";
import EmbryoTransferredTable from "@/components/embyro-transfer/transferred-table.vue";

import BronEmbyroTransfer from "@/components/born/embryo-tranfer.vue";

import { useEmbryoTransferInit } from "@/composable/embryo-transfer";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        EmbyooTransferCatheter,
        EmbryoTransferUltrasound,
        EmbryoTransferTrauman,
        EmbryoTransferPatientDiscomfort,
        EmbryoTransferReload,
        EmbryoTransferTransfer,
        EmbyroTransferFrom,
        EmbryoTransferredTable,
        BronEmbyroTransfer
    },
    setup() {
        const date = ref();
        const dropdownOpts = ref();
        const table = ref([]);

        const {
            store,
            embryoTransfer,
            getEbmryoTransfer,
            saveEmbryoTransfer,
            doctorOptions,
            embryologistOpts,
            daysPostOROptions,
            v$,
            submitted,
            deleteEbryoTransfer,
            openPdfReport
        } = useEmbryoTransferInit();

        const {
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        provide("embryoTransfer", embryoTransfer);

        onMounted(() => {
            store.dispatch("provider/getProviders");
            store.dispatch("embryoTransfer/getFeildOptions");
            getEbmryoTransfer();
        });

        return {
            embryoTransfer,
            saveEmbryoTransfer,
            doctorOptions,
            embryologistOpts,
            daysPostOROptions,
            v$,
            submitted,
            date,
            dropdownOpts,
            table,
            deleteEbryoTransfer,
            openPdfReport,
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission
        };
    }
};
</script>

<style>
#embryo-transfer .p-calendar,
.p-togglebutton {
    height: 24.5px;
}

#embryo-transfer .p-fieldset .p-fieldset-content {
    padding: 0;
}
</style>
