import { DATE_FORMAT_STRING } from "@/config";
import { format } from "date-fns";
import { useToast } from "primevue/usetoast";
import { computed, ref, reactive } from "vue";
import { useStore } from "vuex";
import { useVuelidate } from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { buildPdfUrl } from "@/service/utils";

export function useEmbryoTransferInit() {
    const store = useStore();
    const toast = useToast();
    const embryoTransfer = reactive({
        etId: null,
        cycleId: null,
        patientId: null,
        fertilizationId: null,
        ecId: null,
        etDate: null,
        lotNo: null,
        lotExpDate: null,
        daysPostOR: null,
        catheter: null,
        bloodOnTip: null,
        mucusOnTip: null,
        reload: null,
        notes: null,
        embryologist: null,
        clinician: null,
        trumaMucus: null,
        ultraGuided: null,
        ultraVisual: null,
        discomfort: null,
        transfer: null
    });

    const emptyForm = () => {
        embryoTransfer.etId = null;
        embryoTransfer.cycleId = null;
        embryoTransfer.patientId = null;
        embryoTransfer.fertilizationId = null;
        embryoTransfer.ecId = null;
        embryoTransfer.etDate = null;
        embryoTransfer.lotNo = null;
        embryoTransfer.lotExpDate = null;
        embryoTransfer.daysPostOR = null;
        embryoTransfer.catheter = null;
        embryoTransfer.bloodOnTip = null;
        embryoTransfer.mucusOnTip = null;
        embryoTransfer.reload = null;
        embryoTransfer.notes = null;
        embryoTransfer.embryologist = null;
        embryoTransfer.clinician = null;
        embryoTransfer.trumaMucus = null;
        embryoTransfer.ultraGuided = null;
        embryoTransfer.ultraVisual = null;
        embryoTransfer.discomfort = null;
        embryoTransfer.transfer = null;
    };

    const getEbmryoTransfer = () => {
        if (!store.state.artCycle.selectedCycle) {
            toast.add({
                severity: "warn",
                summary: "",
                detail: "Please select Cycle first",
                life: 3000
            });
            return;
        }
        store
            .dispatch(
                "embryoTransfer/getEmbryoTranasfer",
                store.state.artCycle.selectedCycle.cycleId
            )
            .then((data) => {
                embryoTransfer.etId = data.etId;
                embryoTransfer.cycleId = data.cycleId;
                embryoTransfer.patientId = data.patientId;
                embryoTransfer.fertilizationId = data.fertilizationId;
                embryoTransfer.ecId = data.ecId;
                embryoTransfer.etDate = data.etDate
                    ? new Date(data.etDate + " 00:00:00")
                    : null;

                embryoTransfer.lotNo = data.lotNo;
                embryoTransfer.lotExpDate = data.lotNo
                    ? new Date(data.lotNo + " 00:00:00")
                    : null;

                embryoTransfer.daysPostOR = data.daysPostOR;
                embryoTransfer.catheter = data.catheter;
                embryoTransfer.bloodOnTip = data.bloodOnTip;
                embryoTransfer.mucusOnTip = data.mucusOnTip;
                embryoTransfer.reload = data.reload;
                embryoTransfer.notes = data.notes;
                embryoTransfer.embryologist = data.embryologist;
                embryoTransfer.clinician = data.clinician;
                embryoTransfer.trumaMucus = data.trumaMucus;
                embryoTransfer.ultraGuided = data.ultraGuided;
                embryoTransfer.ultraVisual = data.ultraVisual;
                embryoTransfer.discomfort = data.discomfort;
                embryoTransfer.transfer = data.transfer;
            })
            .catch((err) => {})
            .finally(() => {});
    };

    const rules = { etDate: { required } };
    const submitted = ref(false);
    const v$ = useVuelidate(rules, embryoTransfer);

    const saveEmbryoTransfer = () => {
        submitted.value = true;
        if (v$.value.etDate.$invalid) {
            return;
        }

        let et = {
            ...embryoTransfer,
            etDate: embryoTransfer.etDate
                ? format(embryoTransfer.etDate, DATE_FORMAT_STRING)
                : null,
            lotExpDate: embryoTransfer.lotExpDate
                ? format(embryoTransfer.lotExpDate, DATE_FORMAT_STRING)
                : null
        };
        if (embryoTransfer.etId) {
            updateEmbryoTransfer(et);
        } else {
            createEmbryoTransfer({
                ...et,
                cycleId: store.state.artCycle.selectedCycle.cycleId,
                patientId: store.state.artCycle.selectedCycle.patientId
            });
        }
    };

    const updateEmbryoTransfer = (et) => {
        store
            .dispatch("embryoTransfer/updateEmbryoTransfer", et)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Update Successful",
                    life: 3000
                });
            })
            .catch(() => console.log("error"))
            .finally(() => {
                submitted.value = false;
            });
    };

    const createEmbryoTransfer = (et) => {
        store
            .dispatch("embryoTransfer/createEmbryoTransfer", et)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Create Successful",
                    life: 3000
                });
            })
            .catch(() => console.log("error"))
            .finally(() => {
                submitted.value = false;
            });
    };

    const embryologistOpts = computed(() => store.state.doctor.embryologists);
    const doctorOptions = computed(() => store.state.provider.providers);
    const daysPostOROptions = computed(
        () => store.state.embryoTransfer.daysPostORList
    );

    const deleteEbryoTransfer = () => {
        store.dispatch("embryoTransfer/deleteEmbryoTranfer").then(() => {
            toast.add({
                severity: "success",
                summary: "",
                detail: "Update Successful",
                life: 3000
            });
            emptyForm();
        });
    };

    const openPdfReport = () => {
        const params = {
            cycleId: store.state.artCycle.selectedCycle.cycleId,
            cycleDate: store.state.artCycle.selectedCycle.cycleDate.split(
                " "
            )[0],
            ecId: null
        };
        let url = buildPdfUrl("Embryo_Transfer", params);
        window.open(url);
    };

    return {
        store,
        embryoTransfer,
        getEbmryoTransfer,
        saveEmbryoTransfer,
        doctorOptions,
        daysPostOROptions,
        v$,
        submitted,
        deleteEbryoTransfer,
        openPdfReport,
        embryologistOpts
    };
}
